.popup {

	&__content {
		.select {
			width: 100%;

			.select2-container {
				width: 100%!important;
			}

			& + .select {
				margin-top: 30px;

				@media (max-width: $w_mob_tab) {
					margin-top: 10px;
				}
			}
		}
	}

	&__title {
		margin-bottom: 22px;

		font-size: 28px;
		font-weight: 600;

		@media (max-width: $w_mob_tab) {
			font-size: 18px;
			margin-bottom: 16px;
		}
	}

	&__btn {
		margin-top: 30px;

		text-align: center;
	}
}